@font-face {
    font-family: 'GraphikLC-Regular';
    src: url('./fonts/GraphikLC-Regular.eot');
    src: url('./fonts/GraphikLC-Regular.woff2') format('woff2'),
    url('./fonts/GraphikLC-Regular.woff') format('woff'),
    url('./fonts/GraphikLC-Regular.ttf') format('truetype'),
    url('./fonts/GraphikLC-Regular.eot?#iefix') format('embedded-opentype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'GraphikLC-Bold';
    src: url('./fonts/GraphikLC-Bold.eot');
    src: url('./fonts/GraphikLC-Bold.woff2') format('woff2'),
    url('./fonts/GraphikLC-Bold.woff') format('woff'),
    url('./fonts/GraphikLC-Bold.ttf') format('truetype'),
    url('./fonts/GraphikLC-Bold.eot?#iefix') format('embedded-opentype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Sequel 100 Wide 95 Wide';
    src: url('./fonts/Sequel100Wide-95Wide.eot');
    src: url('./fonts/Sequel100Wide-95Wide.eot?#iefix') format('embedded-opentype'),
    url('./fonts/Sequel100Wide-95Wide.woff2') format('woff2'),
    url('./fonts/Sequel100Wide-95Wide.woff') format('woff'),
    url('./fonts/Sequel100Wide-95Wide.ttf') format('truetype');
    font-weight: 900;
    font-style: normal;
}


